import React from 'react';
import PropTypes from 'prop-types';

import "./Section3.scss";

export default function Section3(props) {
  const { className, style } = props;
  return (
    <div id="LandingSection3" className={className} style={style}>
      <div className="top">
        <h2>نحوه کار ما</h2>
        <sub>به جای اینکه محصولتان را در کنار خیابان ها و جلوی درب فروشگاه ها پروموت کنید، با سمپلیت</sub>
      </div>
      <div className="bottom">
        {/** item 1 */}
        <div className="item">
          <div className="fabric">
            <div className="image d-flex-all-center">
              <img
                alt="fabric"
                src="img/washing-hands.png"
                srcSet="img/washing-hands@2x.png 2x,img/washing-hands@3x.png 3x"
              />
            </div>
          </div>
          <div className="text">
            <h5>انجام عملیات</h5>
            <sub>از طریق وب و یا اپلیکیشن</sub>
          </div>
        </div>

        {/** item 2 */}
        <div className="item">
          <div className="fabric">
            <div className="image d-flex-all-center">
              <img
                alt="fabric"
                src="img/check.png"
                srcSet="img/check@2x.png 2x,img/check@3x.png 3x"
              />
            </div>
          </div>
          <div className="text">
            <h5>ثبت درخواست</h5>
            <sub>از طریق وب و یا اپلیکیشن</sub>
          </div>
        </div>

        {/** item 3 */}
        <div className="item">
          <div className="fabric">
            <div className="image d-flex-all-center">
              <img
                alt="fabric"
                src="img/map-location.png"
                srcSet="img/map-location@2x.png 2x,img/map-location@3x.png 3x"
              />
            </div>
          </div>
          <div className="text">
            <h5>انتخاب آدرس و زمان</h5>
            <sub>از طریق وب و یا اپلیکیشن</sub>
          </div>
        </div>

        {/** item 4 */}
        <div className="item">
          <div className="fabric">
            <div className="image d-flex-all-center">
              <img
                alt="fabric"
                src="img/tap.png"
                srcSet="img/tap@2x.png 2x,img/tap@3x.png 3x"
              />
            </div>
          </div>
          <div className="text">
            <h5>انتخاب سرویس</h5>
            <sub>از طریق وب و یا اپلیکیشن</sub>
          </div>
        </div>
      </div>
    </div>
  );
}

Section3.propTypes = {
  className: PropTypes.string,
  style: PropTypes.shape(),
}
Section3.defaultProps = {
  className: "",
  style: {},
}