import React from 'react';
import PropTypes from 'prop-types';
import { Button, NavLink } from '../../components';

import "./Header.scss";

export default function Header(props) {
  const { small } = props;
  return (
    <div className="header">
      <div className="nav-left">
        <div className="nav-item">
          <Button className="d-flex-all-center register-btn" hasborder>
            <img
              src="img/locked-padlock.png"
              alt="register/login"
              srcSet="img/locked-padlock@2x.png 2x, img/locked-padlock@3x.png 3x"
              className="locked-padlock"
            />&nbsp;
            {
              !small &&
              <bdi>ورود/ثبت نام</bdi>
            }
          </Button>
        </div>
      </div>
      <div className="nav-right">
        <div className="nav-group">
          <div className="nav-item nav-logo">
            <img
              src="logo/logo.png"
              alt="logo"
              className="locked-padlock"
            />&nbsp;
            {
              !small &&
              <bdi>کلیـن‌آپ</bdi>
            }
          </div>
          <div className="nav-item"><Button border color="secondary">معرفی</Button></div>
          <div className="nav-item"><NavLink to="#">ثبت درخواست</NavLink></div>
          <div className="nav-item"><NavLink to="#">دانلود اپ</NavLink></div>
        </div>
      </div>
    </div>
  );
}

Header.propTypes = {
  small: PropTypes.bool.isRequired,
}