import React from 'react';
import PropTypes from 'prop-types';

import "./Button.scss";

/**
 * Button Component
 * @augments {Component<Props>}
 */
export default function Button(props) {
  const { className, style, hasborder, shadow, color, size } = props;
  return (
    <button
      style={style}
      className={
        `${String(className).trim()} cleanup-btn cleanup-btn-${color} cleanup-btn-${size} cleanup-btn-shadow-${shadow} ${hasborder ? "cleanup-btn-border" : ""}`
      }
    >
      {props.children}
    </button>
  );
}

Button.propTypes = {
  hasborder: PropTypes.bool,
  className: PropTypes.string,
  style: PropTypes.shape(),
  shadow: PropTypes.oneOf(["none", "self", "lighter", "light", "mod"]),
  color: PropTypes.oneOf(["primary", "secondary", "tertiary"]),
  size: PropTypes.oneOf(["small", "medium", "large"]),
}

Button.defaultProps = {
  className: "",
  style: {},
  hasborder: false,
  shadow: "none",
  color: "primary",
  size: "small",
}