import React from 'react';
import PropTypes from 'prop-types';
import { Button, Slider } from '../../components';

import "./Section1.scss";

export default function Section1(props) {
  const { className, style } = props;
  return (
    <div id="LandingSection1" className={className} style={style}>
      <div className="left">
        <div className="slider-parent">
          <Slider className="slider" />
        </div>
      </div>
      <div className="right">
        <h1 dir="rtl">با کلین‌آپ تمیز بمانید</h1>
        <p dir="rtl">لورم ایپسوم متن ساختگی با تولید سادگی نامفهوم از صنعت چاپ و با استفاده از طراحان گرافیک است نیاز و کاربردهای متنوع با هدف بهبود ابزارهای کاربردی می باشد. </p>
        <div className="group-btns">
          <Button color="primary" shadow="light" size="medium">خدمات کلین‌آپ</Button>
          <Button color="tertiary" shadow="self" size="medium">ثبت درخواست</Button>
        </div>
      </div>
    </div>
  );
}

Section1.propTypes = {
  className: PropTypes.string,
  style: PropTypes.shape(),
}
Section1.defaultProps = {
  className: "",
  style: {},
}