import React from 'react';
import { BrowserRouter, Switch, Route, Redirect } from 'react-router-dom';
import {Header, Footer, P404} from './views';
import Index from "./pages/index";

import './App.css';

function App() {
  const small = window.innerWidth <= 960;
  return (
    <div className="App">
      <BrowserRouter basename={process.env.PUBLIC_URL}>
        <header id="Header"><Header small={small} /></header>
        <main id="Main">
          <Switch>
            <Redirect exact from="/home" to="/" />
            <Route exact path="/" render={() => <Index small={small} />} />
            <Route path="*" render={() => <P404 />} />
          </Switch>
        </main>
        <footer id="Footer"><Footer /></footer>
      </BrowserRouter>
    </div>
  );
}

export default App;
