import React from 'react';
import PropTypes from 'prop-types';
import triangle from '../../assets/images/triangle.svg';

import "./Slider.scss";

/**
 * Slider Component
 * _-- This component is a temporary one & must be modified and completed ! --_
 * @augments {Component<Props>}
 */
export default function Slider(props) {
  const { className, style } = props;
  return (
    <div
      style={style}
      className={
        `${String(className).trim()} cleanup-slider`
      }
    >
      <div className="displayer">
        <svg className="svg" width="90" height="90">
          <circle cx="45" cy="45" r="40" strokeWidth="1" className="circle" />
        </svg>
        <img className="svg triangle" src={triangle} alt="triangle"/>
        <img
          src="img/data-3-1-2-x.png"
          srcSet="img/data-3-1-2-x@2x.png 2x, img/data-3-1-2-x@3x.png 3x"
          alt="displayer"
          className="image"
        />
      </div>
      <div className="thums">
        <div className="thum" />
        <div className="thum active" />
        <div className="thum" />
      </div>
    </div>
  );
}

Slider.propTypes = {
  className: PropTypes.string,
  style: PropTypes.shape(),
}

Slider.defaultProps = {
  className: "",
  style: {},
}