import React from 'react';
import PropTypes from 'prop-types';
import {LandingSection1, LandingSection2, LandingSection3, LandingSection4} from "../views";

import "../assets/foundation/index.scss";

export default function Index(props) {
  const {small} = props;
  return (
    <div id="Index">
      <LandingSection1 className="section1" />
      <LandingSection2 className="section2" small={small} />
      <LandingSection3 className="section3" />
      <LandingSection4 className="section4" />
    </div>
  );
}

Index.propTypes = {
  small: PropTypes.bool.isRequired,
}