import React from 'react';
import PropTypes from 'prop-types';
import {Button} from "../../components";

import "./Section4.scss";

export default function Section4(props) {
  const {className, style} = props;
  return (
    <div id="LandingSection4" className={className} style={style}>
      <div className="left"></div>
      <div className="center">
        <div className="top">
          <h2>ثبت درخواست کلین‌آپ</h2>
          <sub>به جای اینکه محصولتان را در کنار خیابان ها و جلوی درب فروشگاه ها پروموت کنید، با سمپلیت</sub>
        </div>
        <div className="bottom">
          <Button color="tertiary" shadow="self" size="large">ثبت درخواست</Button>
        </div>
      </div>
      <div className="right"></div>
    </div>
  );
}

Section4.propTypes = {
  className: PropTypes.string,
  style: PropTypes.shape(),
}
Section4.defaultProps = {
  className: "",
  style: {},
}