import React from 'react';
import {NavLink} from '../../components';

import './Footer.scss';

export default function Footer() {
  const navs = [
    {
      title: "صفحات مفید",
      items: [
        {text: "نیکوکاری", url: "#"},
        {text: "راهنمای استفاده از کدمعرف", url: "#"},
      ]
    },
    {
      title: "خدمات کلین‌آپ ها",
      items: [
        {text: "خشکشویی", url: "#"},
        {text: "کارواش", url: "#"},
        {text: "شستشو در منزل", url: "#"},
      ]
    },
    {
      title: "راهنمای کاربران",
      items: [
        {text: "درباره کلین‌آپ", url: "#"},
        {text: "تماس با ما", url: "#"},
      ]
    }
  ]
  return (
    <nav className="footer">
      <div className="left">
        <span><bdi>راه های ارتباطی با کلین‌آپ</bdi>&nbsp; <i className="fas fa-phone-square" /></span>
        <span className="tel"><bdi>021-5692</bdi></span>
        <span className="mail"><bdi>info@cleanup.com</bdi></span>
        <span className="social">
          <span><i className="fab fa-telegram-plane" /></span>
          <span><i className="fab fa-twitter" /></span>
          <span><i className="fab fa-instagram" /></span>
        </span>
      </div>
      <div className="right">
        <div className="signs">
          <div className="sign">
            <img
              alt="anjoman-senfi"
              src="img/pics-art-04-12-12-24-12-370-x-245.png"
              srcSet="img/pics-art-04-12-12-24-12-370-x-245@2x.png 2x, img/pics-art-04-12-12-24-12-370-x-245@3x.png 3x"
              className="PicsArt_04-12-122412-370x245"
            />
          </div>
          <div className="sign">
            <img
              alt="anjoman-senfi"
              src="img/enamad.png"
              srcSet="img/enamad.png 2x, img/enamad.png 3x"
              className="PicsArt_04-12-122412-370x245"
            />
          </div>
        </div>
        <div className="navs">
          {
            (navs||[]).map((nav) => (
              <div key={nav.title} className="nav">
                <h3>{nav.title}</h3>
                <ul>
                  {
                    (nav.items||[]).map((item) => (
                      <li key={item.text}><NavLink to={item.url}>{item.text}</NavLink></li>
                    ))
                  }
                </ul>
              </div>
            ))
          }
        </div>
      </div>
      <div className="bottom">
        <div className="logo">
          <img
            src="logo/logo.png"
            alt="logo"
            className="locked-padlock"
          />&nbsp;
            <bdi>کلیـن‌آپ</bdi>
        </div>
      </div>
    </nav>
  );
}
